@import url('https://fonts.googleapis.com/css2?family=Agbalumo&family=Alumni+Sans+Inline+One&family=DM+Serif+Display&family=Edu+TAS+Beginner:wght@600&family=Mochiy+Pop+One&family=Rubik:ital@1&display=swap');


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  user-select: none;
}
/* ================================================
===============HEADER AND FOOTER CSS=============== */
.navbar{
  font-family: 'Rubik', sans-serif;
  background-color: rgba(209, 209, 209, 0.98) !important;
  box-shadow: 0 8px 6px -6px grey;
  user-select: none;
}
.navbar a{
  color: rgb(70, 69, 69) !important;
  margin: 3px;
}
.active{
  font-weight: bolder;
}
.footerLink:hover{
  color: aliceblue;
}
.footerLink{
  text-decoration: none;
  margin: 6px;
}

/* =================================================
============Login register and forget CSS============ */
.auth_container{
  height: 100vh;
  width: 100vw;
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-image: url("/public/images/Register_login.jpeg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

.auth_form_container{
  width:80%;
  height:70%;
  background-color: antiquewhite;
  display: flex;
  align-items: center;
  justify-content: center;
}

.auth_login{
  height: 100%;
  width: 60%;
  clip-path: polygon(0 0 , 90% 0%, 100%  100% , 0% 100% );
  background-image: url("/public/images/Login_form_side_img.jpeg");
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}

@media screen and (max-width : 750px) {
  .auth_login{
    display: none;
  }
  .auth_form{
    width: 80% !important;
  }
  .auth_form_container{
    border-radius: 50px;
  }
}

.auth_form{
  text-align: center;
  width: 40%;
}
.auth_form_container h3{
  font-family: Rubik ;
  font-weight: bolder;
  text-shadow: 3px 3px 2px grey;
}
.auth_form form{
  width: 90%;
  margin: auto;
}
.auth_form form input {
  margin: 6% 0px;
  outline: none !important;
  border: none;
  border-radius: 0px;
  background-color: #ffffff00;
  border-bottom: 1px solid black;
}
.auth_form form input:focus{
  box-shadow: none;
  border-radius: 0px !important;
  font-weight: bold;
  background-color: #ffffff00 !important;
  border-bottom: 2px solid black !important;
}
.auth_Page_Switcher{
  width: fit-content;
  margin: 2% auto;
  background-color: transparent;
  margin-top: 1%;
  color: black;
  border: 2px solid black;
  border-radius: 90px;
  padding: 1.2%;
  transition-duration: 1.2s ;
}
.auth_Page_Switcher:hover{
  background-color: black;
  color: white;
  transform: scale(1.2);
  cursor: pointer;
}
.btn-primary{
  background-color: #0078bf;
  color: white;
  font-weight: bolder;
  border: none;
  transition-duration: 1s;
}
.btn-primary:hover{
  background-color: #d958fa;
  border: none;
  transform: scale(1.15);
}

/* ==========================================================
=========HOME PAGE CSS======================================= */

.home_Page{
  height: 100vh;
  width: 100vw;
  position: fixed;
  left: 0;
  top: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color:rgb(46, 46, 46);
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow-y: auto;
}
.home_container{
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  overflow-y: auto;
  padding: 12vh 0;
}
.home_text{
  width: 95%;
  padding-bottom: 3vh;
}
.home_text h2{
  color: red;
  font-family: 'Times New Roman', Times, serif;
}
.home_text h1{
  color: gold;
  padding-left: 13px;
  font-family: Alumni Sans Inline One;
  font-size: 3.5rem;
}
.home_text h5{
  color: white;
}
.home_Img{
  background-image: url("/public/images/My-Img.png");
  background-color: #0078bf;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 350px;
  width: 250px;
  border-radius: 150px;
  border: 2px solid white;
}


/* ======================================================================
=============Order Page CSS============================================== */
.orderContainer{
  text-align: center;
}

.orderPage{
  display: inline-block;
  width: 80vw;
  margin: auto;
}


/* ===================================================================================
==============OrderPage CSS=========================================================== */
.orderCard{
  width: 90%;
  margin: 1% auto;
  background-color: rgba(204, 211, 214, 0.342);
}
.orderHeading{
  justify-content: space-around;
  font-family: 'Times New Roman', Times, serif;
}

.about_img{
  background-image: url("/public/images/My-Img.png");
  background-color: #0078bf;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 350px;
  width: 280px;
  border: 2px solid white;
}